/**
 * WordPress dependencies
 */
import { ReactSVG } from 'react-svg'
import { RichTextToolbarButton } from '@wordpress/block-editor';
import { __ } from '@wordpress/i18n';
import { registerFormatType } from '@wordpress/rich-text';
// import methods - avoid not a function error?
import { toggleFormat } from '@wordpress/rich-text';

const type = 'gutenberg-button/tooltip';

const tooltipAction = ( { value } ) => (
	<RichTextToolbarButton
		icon='format-status'
		title={ __( 'Tooltip', 'dahtheme' ) }
		onClick={ () => { processTooltip( value ) } }
	/>
);

registerFormatType( type, {
	title: __( 'Tooltip', 'dahtheme' ),
	tagName: 'abbr',
	className: 'editor-tooltip',
	attributes: {
		title: 'title',
	},
	edit: tooltipAction
} );

function processTooltip( value ) {
	let selectedText = value.text.substring( value.start, value.end );
	
	value.onChange(
		toggleFormat( 
			selectedText, 
			{ type: type }
		)
	)
	
} //end process()

function process( value ) {
	const selectedText = value.text.substring( value.start, value.end );
	
	if ( 0 === selectedText.length ) {
		return;
	}

	console.log( 'Selection:', selectedText, value );
} //end process()